import React, { useState } from 'react'
import { MainDiv, Basic, TaskList, Tytul, MainGuz, Guz, Rodzajzad } from '../../srednia/srednia.styled';
import TopNavi from '../../../components/topNavi/topNavi.jsx';
import LeftNavi from '../../../components/leftNavi/leftNavi.jsx';
import WorksList from '../../../components/worksList/worksList.jsx';
// import { MathJax } from 'better-react-mathjax';


const WielokatySP = () => {
    const [title, setTitle] = useState(null);
    const [currentTask, setCurrentTask] = useState(null);






    const renderTask = (event) => {
        setTitle(event.target.innerText);
        let targets = event.target.parentElement.childNodes

        for (let i = 0; i < targets.length; i++) {
            if (event.target.id === targets[i].id) {
                targets[i].style.backgroundColor = "green";
            }
            else { targets[i].style.backgroundColor = "#e9e9ed" }
        }
        console.log(event.target.id);
        setCurrentTask(event.target.id);

    };
    // console.log(currentTask);


    return (
        <MainDiv>
            <TopNavi model="a" /> {/* a - podstawwka, b- rednia, c- matura, d- studia*/}
            <Basic>
                <LeftNavi model="a" />
                <TaskList>
                    <Tytul>Wielokty, koa i okrgi</Tytul>
                    <MainGuz data-poddzialy>
                        <Guz id="1" onClick={renderTask}>Trjkty</Guz>
                        <Guz id="2" onClick={renderTask}>Czworokty</Guz>
                        <Guz id="3" onClick={renderTask}>Rysowanie figur w skali</Guz>
                        <Guz id="4" onClick={renderTask}>Koo i okrg</Guz>
                        <Guz id="5" onClick={renderTask}>Wielokty i ich wasnoci</Guz>
                        <Guz id="6" onClick={renderTask}>Obwd wielokta</Guz>
                        <Guz id="7" onClick={renderTask}>Symetrie</Guz>
                        <Guz id="8" onClick={renderTask}>Pola wieloktw</Guz>
                        <Guz id="9" onClick={renderTask}>Twierdzenie Pitagorasa</Guz>
                        <Guz id="10" onClick={renderTask}>Wielokty foremne</Guz>
                        <Guz id="11" onClick={renderTask}>Przektna kwadratu. Wysoko i pole trjkta rwnobocznego</Guz>
                        <Guz id="12" onClick={renderTask}>Pole piercienia</Guz>




                    </MainGuz>
                    <TaskList>
                        <Rodzajzad>{title}</Rodzajzad>
                        <WorksList model={currentTask} dzial="sp9" />
                    </TaskList>
                </TaskList>

            </Basic>
        </MainDiv>


    )
}

export default WielokatySP