import React, { useState } from 'react'
import { MainDiv, Basic, TaskList, Tytul, Rodzajzad, MainGuz, Guz } from '../srednia.styled';
import TopNavi from '../../../components/topNavi/topNavi';
import LeftNavi from '../../../components/leftNavi/leftNavi';
import WorksList from '../../../components/worksList/worksList';
// import { current } from '@reduxjs/toolkit';
import { MathJax } from 'better-react-mathjax';


const Przeksztalcenia = () => {
    const [title, setTitle] = useState(null);
    const [currentTask, setCurrentTask] = useState(null);






    const renderTask = (event) => {
        setTitle(event.target.innerText);

        setCurrentTask(event.target.id);
        let targets = event.target.parentElement.childNodes
        for (let i = 0; i < targets.length; i++) {
            if (event.target.id === targets[i].id) {
                targets[i].style.backgroundColor = "green";
            } else { targets[i].style.backgroundColor = "#e9e9ed" }
        }

    }
    // console.log(currentTask);


    return (
        <MainDiv>
            <TopNavi model="b" /> {/* a - podstawwka, b- rednia, c- matura, d- studia*/}
            <Basic>
                <LeftNavi model="b" />
                <TaskList>
                    <Tytul>Przeksztacenia wykresw funkcji</Tytul>
                    <MainGuz data-poddzialy>
                        <Guz id="1" onClick={renderTask}>Wektor na paszczynie</Guz>
                        <Guz id="2" onClick={renderTask}>Przesunicie wykresu wzdu osi OX</Guz>
                        <Guz id="3" onClick={renderTask}>Przesunicie wykresu wzdu osi OY</Guz>
                        <Guz id="4" onClick={renderTask}>Symetria osiowa wzgldem osi ukadu wsprzdnych</Guz>
                        <Guz id="5" onClick={renderTask}>Symetria rodkowa wzgldem punktu <MathJax inline>{"\\((0,0)\\)"}</MathJax></Guz>
                        <Guz id="6" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> <MathJax inline>{"\\(y=|f(x)|\\)"}</MathJax> oraz <MathJax inline>{"\\(y=f(|x|)\\)"}</MathJax></Guz>
                        <Guz id="7" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> <MathJax inline>{"\\(y=k\\cdot f(x)\\)"}</MathJax> oraz <MathJax inline>{"\\(y=f(k\\cdot x)\\)"}</MathJax></Guz>
                        <Guz id="8" onClick={renderTask}>Szkicowanie wykresw funkcji</Guz>
                       
                    </MainGuz>

                    <TaskList>
                        <Rodzajzad>{title}</Rodzajzad>
                        <WorksList model={currentTask} dzial="8" />
                    </TaskList>
                </TaskList>

            </Basic>
        </MainDiv>

    )
}

export default Przeksztalcenia