import React, { useState } from 'react'
import { MainDiv, Basic, TaskList, Tytul, Rodzajzad, MainGuz, Guz } from '../srednia.styled';
import TopNavi from '../../../components/topNavi/topNavi';
import LeftNavi from '../../../components/leftNavi/leftNavi';
import WorksList from '../../../components/worksList/worksList';
// import { current } from '@reduxjs/toolkit';
import { MathJax } from 'better-react-mathjax';


const CiagiS = () => {
    const [title, setTitle] = useState(null);
    const [currentTask, setCurrentTask] = useState(null);






    const renderTask = (event) => {
        setTitle(event.target.innerText);

        setCurrentTask(event.target.id);
        let targets = event.target.parentElement.childNodes
        for (let i = 0; i < targets.length; i++) {
            if (event.target.id === targets[i].id) {
                targets[i].style.backgroundColor = "green";
            } else { targets[i].style.backgroundColor = "#e9e9ed" }
        }

    }
    // console.log(currentTask);


    return (
        <MainDiv>
            <TopNavi model="b" /> {/* a - podstawwka, b- rednia, c- matura, d- studia*/}
            <Basic>
                <LeftNavi model="b" />
                <TaskList>
                    <Tytul>Cigi</Tytul>
                    <MainGuz data-poddzialy>
                        <Guz id="1" onClick={renderTask}>Okrelenie i sposoby opis cigu</Guz>
                        <Guz id="2" onClick={renderTask}>Monotoniczno cigu</Guz>
                        <Guz id="3" onClick={renderTask}>Cig arytmetyczny</Guz>
                        <Guz id="4" onClick={renderTask}>Cig geometryczny</Guz>
                        <Guz id="5" onClick={renderTask}>Zadania rne</Guz>
                        <Guz id="6" onClick={renderTask}>Lokaty pienine i kredyty bankowe</Guz>
                        <Guz id="7" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Granica cigu liczbowego</Guz>
                        <Guz id="8" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Obliczanie granic cigw zbienych i ich wasnoci</Guz>
                        <Guz id="9" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Cigi rozbiene</Guz>
                        <Guz id="10" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Szereg geometryczny</Guz>

                    </MainGuz>

                    <TaskList>
                        <Rodzajzad>{title}</Rodzajzad>
                        <WorksList model={currentTask} dzial="14" />
                    </TaskList>
                </TaskList>

            </Basic>
        </MainDiv>

    )
}

export default CiagiS