import React, { useState } from 'react'
import { MainDiv, Basic, TaskList, Tytul, Rodzajzad, MainGuz, Guz } from '../srednia.styled';
import TopNavi from '../../../components/topNavi/topNavi';
import LeftNavi from '../../../components/leftNavi/leftNavi';
import WorksList from '../../../components/worksList/worksList';
// import { current } from '@reduxjs/toolkit';
import { MathJax } from 'better-react-mathjax';


const Analityczna = () => {
    const [title, setTitle] = useState(null);
    const [currentTask, setCurrentTask] = useState(null);






    const renderTask = (event) => {
        setTitle(event.target.innerText);

        setCurrentTask(event.target.id);
        let targets = event.target.parentElement.childNodes
        for (let i = 0; i < targets.length; i++) {
            if (event.target.id === targets[i].id) {
                targets[i].style.backgroundColor = "green";
            } else { targets[i].style.backgroundColor = "#e9e9ed" }
        }

    }
    // console.log(currentTask);


    return (
        <MainDiv>
            <TopNavi model="b" /> {/* a - podstawwka, b- rednia, c- matura, d- studia*/}
            <Basic>
                <LeftNavi model="b" />
                <TaskList>
                    <Tytul>Geometria analityczna</Tytul>
                    <MainGuz data-poddzialy>
                        <Guz id="1" onClick={renderTask}>Odcinek w ukadzie wsprzdnych</Guz>
                        <Guz id="2" onClick={renderTask}>Rwnanie kierunkowe prostej</Guz>
                        <Guz id="3" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Rwnanie prostej w postaci oglnej</Guz>
                        <Guz id="4" onClick={renderTask}>Rwnanie okrgu</Guz>
                        <Guz id="5" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Wyznaczanie punktw wsplnych prostych, okrgw i parabol</Guz>
                        <Guz id="6" onClick={renderTask}>Zastosowanie ukadw rwna do rozwizywania zada z geometrii analitycznej</Guz>
                        <Guz id="7" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Wektor w ukadzie wsprzdnych, podzia odcinka</Guz>
                        <Guz id="8" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Kt midzy wektorami</Guz>
                        <Guz id="9" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Odlego punktu od prostej, odlego midzy prostymi rwnolegymi</Guz>
                        <Guz id="10" onClick={renderTask}>Pole trjkta, pole wieloktw</Guz>
                        <Guz id="11" onClick={renderTask}>Wzajemne pooenie prostej i okrgu</Guz>
                        <Guz id="12" onClick={renderTask}>Wzajemne pooenie dwch okrgw</Guz>
                        <Guz id="13" onClick={renderTask}>Zadania rne</Guz>
                        <Guz id="14" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Wybrane przeksztacenia geometryczne w ukadzie wsprzdnych</Guz>
                        <Guz id="15" onClick={renderTask}><MathJax inline>{"\\(\\star\\)"}</MathJax> Zastosowanie analizy matematycznej w rozwizywaniu zada z geometrii analitycznej</Guz>
                    </MainGuz>

                    <TaskList>
                        <Rodzajzad>{title}</Rodzajzad>
                        <WorksList model={currentTask} dzial="11" />
                    </TaskList>
                </TaskList>

            </Basic>
        </MainDiv>

    )
}

export default Analityczna